import angular from 'angular';
import uiRouter from 'angular-ui-router';
import productComponent from './product.component';

let productModule = angular.module('product', [
  uiRouter
])

  .config(($stateProvider, $urlRouterProvider) => {
    "ngInject";

    $stateProvider
      .state('product', {
        url: '/product/:id',
        component: 'product'
      });
  })

  .component('product', productComponent)
  .constant('products', [{"id": 1, "name": "Product 1", "value": "2.55", "description": "Description 1"},
    {"id": 2, "name": "Product 2", "value": "4.99", "description": "Description 2"},
    {"id": 3, "name": "Product 3", "value": "29.90", "description": "Description 3"},
    {"id": 4, "name": "Product 4", "value": "20.00", "description": "Description 4"},
    {"id": 5, "name": "Product 5", "value": "8.88", "description": "Description 5"},
    {"id": 6, "name": "Product 6", "value": "15.00", "description": "Description 6"},
    {"id": 7, "name": "Product 7", "value": "100.00", "description": "Description 7"},
    {"id": 8, "name": "Product 8", "value": "200.00", "description": "Description 8"},
    {"id": 9, "name": "Product 9", "value": "500.00", "description": "Description 9"},])

  .name;

export default productModule;



